/* ===== Catedras.css (simple y limpio) ===== */

:root{
  --bg: #0f1220;         /* fondo */
  --card: #171a2b;       /* tarjetas / contenedores */
  --text: #e7e9ee;       /* texto principal */
  --muted: #a8b0c3;      /* texto suave */
  --accent: #4f8cff;     /* acento */
  --accent-2: #22c55e;   /* acento secundario */
  --border: #2a2f45;     /* bordes */
  --shadow: 0 8px 24px rgba(0,0,0,.35);
}

* { box-sizing: border-box; }

.catedras-page{
  min-height: 100vh;
  background: radial-gradient(1200px 800px at 80% -10%, rgba(79,140,255,.15), transparent 60%),
              radial-gradient(900px 600px at -10% 30%, rgba(34,197,94,.15), transparent 60%),
              var(--bg);
  color: var(--text);
  padding: 24px;
}

.catedras-header{
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  gap: 16px;
  margin: 0 auto 16px;
  max-width: 1200px;
}

.btn-volver{
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 10px 14px;
  font-weight: 600;
  background: transparent;
  color: var(--text);
  border: 1px solid var(--border);
  border-radius: 10px;
  cursor: pointer;
  transition: .2s ease;
}
.btn-volver:hover{ border-color: var(--accent); transform: translateY(-1px); }

.catedras-title{
  margin: 0;
  font-size: clamp(20px, 2.4vw, 28px);
  font-weight: 800;
  letter-spacing: .2px;
  text-align: center;
}
.catedras-title svg{ margin-right: 8px; color: var(--accent); }

.catedras-search{
  display: flex;
  align-items: center;
  gap: 10px;
  justify-self: end;
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 8px 12px;
  box-shadow: var(--shadow);
}
.catedras-search .search-icon{ color: var(--muted); }
.catedras-search input{
  background: transparent;
  border: 0;
  outline: 0;
  color: var(--text);
  min-width: 260px;
}
.catedras-search input::placeholder{ color: var(--muted); }

/* ---------- Contenido ---------- */
.catedras-content{
  max-width: 1200px;
  margin: 0 auto;
}

.catedras-table-wrapper{
  background: linear-gradient(180deg, rgba(255,255,255,.02), rgba(255,255,255,0)) ,
              var(--card);
  border: 1px solid var(--border);
  border-radius: 16px;
  box-shadow: var(--shadow);
  overflow: hidden;
}

/* Tabla */
.catedras-table{
  width: 100%;
  border-collapse: collapse;
  font-size: 15px;
}

.catedras-table thead th{
  position: sticky;
  top: 0;
  background: #1b1f33;
  color: var(--muted);
  text-align: left;
  font-weight: 700;
  padding: 14px 16px;
  border-bottom: 1px solid var(--border);
  z-index: 1;
}

.catedras-table tbody td{
  padding: 12px 16px;
  border-bottom: 1px solid rgba(255,255,255,.06);
}

.catedras-table tbody tr{
  transition: background .15s ease;
}
.catedras-table tbody tr:hover{
  background: rgba(79,140,255,.08);
}

/* Scroll cuando hay muchas filas */
.catedras-table-wrapper{
  max-height: 70vh;      /* ajust si quers ms alto */
  overflow: auto;
}

/* ------- Responsivo ------- */
@media (max-width: 860px){
  .catedras-header{
    grid-template-columns: 1fr;
    gap: 12px;
  }
  .catedras-title{ text-align: left; }
  .catedras-search{ justify-self: start; width: 100%; }
  .catedras-search input{ min-width: 0; width: 100%; }
}

@media (max-width: 620px){
  .catedras-table thead{ display: none; }
  .catedras-table, .catedras-table tbody, .catedras-table tr, .catedras-table td{
    display: block;
    width: 100%;
  }
  .catedras-table tr{ border-bottom: 1px solid var(--border); }
  .catedras-table td{
    padding: 10px 14px;
  }
  .catedras-table td::before{
    content: attr(data-label);
    display: block;
    font-size: 12px;
    color: var(--muted);
    margin-bottom: 4px;
    text-transform: uppercase;
    letter-spacing: .6px;
  }
}

